// Extended school interface that includes additional UI state
export interface School {
  id: string;
  name: string;
  latitude: number;
  longitude: number;
  address?: string;
  distance?: number; // in meters
  isAssigned?: boolean;
  radius?: number; // in meters, default 100
}

// Convert static data to School format
const convertToSchools = (): School[] => {
  return SCHOOLS_DATA.map((school, index) => ({
    id: `school-${index + 1}`,
    name: school.name,
    latitude: school.latitude,
    longitude: school.longitude,
    address: `${school.name} Location`, // Placeholder address
    radius: 100, // Default 100 meter radius
    isAssigned: false, // Will be updated based on user assignments
  }));
};

// Service class for school operations
export class SchoolService {
  private static schools: School[] | null = null;

  // Get all available schools
  static async getAllSchools(): Promise<School[]> {
    if (!this.schools) {
      this.schools = convertToSchools();
    }
    return [...this.schools];
  }

  // Get schools assigned to a specific provider
  static async getAssignedSchools(providerId: string): Promise<School[]> {
    // For now, we'll simulate assignments by returning first 3 schools
    // Later this will integrate with Firestore user assignments
    const allSchools = await this.getAllSchools();

    // Mock assignment logic - in real implementation, this would check user's assignedLocations
    const assignedSchoolIds = ['school-1', 'school-2', 'school-3'];

    return allSchools
      .filter(school => assignedSchoolIds.includes(school.id))
      .map(school => ({ ...school, isAssigned: true }));
  }

  // Get school by ID
  static async getSchoolById(schoolId: string): Promise<School | null> {
    const schools = await this.getAllSchools();
    return schools.find(school => school.id === schoolId) || null;
  }

  // Calculate distance between user location and school
  static calculateDistance(
    userLat: number,
    userLng: number,
    schoolLat: number,
    schoolLng: number
  ): number {
    const R = 6371e3; // Earth's radius in meters
    const φ1 = (userLat * Math.PI) / 180;
    const φ2 = (schoolLat * Math.PI) / 180;
    const Δφ = ((schoolLat - userLat) * Math.PI) / 180;
    const Δλ = ((schoolLng - userLng) * Math.PI) / 180;

    const a =
      Math.sin(Δφ / 2) * Math.sin(Δφ / 2) +
      Math.cos(φ1) * Math.cos(φ2) * Math.sin(Δλ / 2) * Math.sin(Δλ / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

    return R * c; // Distance in meters
  }

  // Check if user is within school radius
  static isWithinRadius(
    userLat: number,
    userLng: number,
    school: School
  ): boolean {
    const distance = this.calculateDistance(
      userLat,
      userLng,
      school.latitude,
      school.longitude
    );
    return distance <= (school.radius || 100);
  }

  // Get schools with calculated distances from user location
  static async getSchoolsWithDistance(
    userLat: number,
    userLng: number,
    providerId: string
  ): Promise<School[]> {
    const assignedSchools = await this.getAssignedSchools(providerId);

    return assignedSchools.map(school => ({
      ...school,
      distance: this.calculateDistance(
        userLat,
        userLng,
        school.latitude,
        school.longitude
      )
    })).sort((a, b) => (a.distance || 0) - (b.distance || 0));
  }

  // Search schools by name
  static async searchSchools(query: string, providerId?: string): Promise<School[]> {
    const schools = providerId
      ? await this.getAssignedSchools(providerId)
      : await this.getAllSchools();

    if (!query.trim()) {
      return schools;
    }

    const lowercaseQuery = query.toLowerCase();
    return schools.filter(school =>
      school.name.toLowerCase().includes(lowercaseQuery)
    );
  }
}

export default SchoolService;
